<template>
    <h1>GitHub</h1>
    <input type="text" placeholder="Repository Name:" v-model="repo" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Owner:" v-model="owner" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Issue Title:" v-model="title" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Content:" v-model="body"/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getGithub">Confirm</button>
</template>

<script>    
export default {
    name: "GithubREA",
    data() {
        return {
            repo: null,
            owner : null,
            title: null,
            body: null,
        }
    },
    methods: {
        getGithub() {
            if (this.repo != null && this.title != null && this.owner != null && this.body != null) {
                let tmpREA = []
                let tmpNREA = []
                let R = {
                    "repo": this.repo,
                    "owner": this.owner,
                    "title": this.title,
                    "body": this.body
                }
                let REA = {
                    "content": JSON.stringify(R),
                    "id": "636277770bf8e24d4f8155ee",
                }
                let nameRea = {
                    "name": "Github",
                    "description": "Create an issue on a GitHub repository"
                }
                if (localStorage.reaction)
                    tmpREA = JSON.parse(localStorage.reaction)
                if (localStorage.namerea)
                    tmpNREA = JSON.parse(localStorage.namerea)
                tmpREA.push(REA)
                tmpNREA.push(nameRea)
                localStorage.reaction = JSON.stringify(tmpREA)
                localStorage.namerea = JSON.stringify(tmpNREA)
                var myHeaders = new Headers();
                myHeaders.append("token", localStorage.token);
                var requestOptions = {
                  method: 'GET',
                  headers: myHeaders,
                  redirect: 'follow'
                };
                fetch(localStorage.server + "/user/github/verify", requestOptions)
                    .then(res => {
                        res.json().then((ans) => {
                            if (ans["status"] == "ok") {
                              this.$router.push({ name: 'Recap' });
                            }
                            else {
                              localStorage.path = 'Recap';
                            
                              var requestOptions = {
                                method: 'GET',
                                headers: myHeaders,
                                redirect: 'follow'
                              };
                          
                              fetch(localStorage.server + "/user/github/getLink", requestOptions)
                                .then(rep => {
                                    if (rep.status === 200) {
                                        rep.text().then((ans) => {
                                            window.location.href = ans;
                                        })
                                    }
                                })
                                .catch(error => console.log('error', error));
                            }
                        })
                    })
                  .catch(error => console.log('error', error));
            } else {
                alert('Not all information filled.')
            }
        },
    }
}
</script>

